.timer-container {
  position: relative; }

.timer-label {
  width: 300px;
  position: absolute;
  left: 0;
  top: calc(50% - 30px);
  line-height: 1;
  text-align: center;
  font-family: sans-serif;
  font-weight: 700;
  font-size: 44px;
  color: #54e8b5; }
  .medium .timer-label {
    color: #ffc107; }
  .high .timer-label {
    color: #f44336; }

.timer-label.timeup {
  color: #a2a2a2; }



